import produce from "immer";
var INITIAL_STATE = {
    items: []
};
var cart = function (state, action) {
    if (state === void 0) { state = INITIAL_STATE; }
    return produce(state, function (draft) {
        switch (action.type) {
            case "ADD_PRODUCT_TO_CART": {
                var product_1 = action.payload.product;
                var productInCartIndex = draft.items.findIndex(function (item) {
                    return item.product.id === product_1.id;
                });
                if (productInCartIndex >= 0) {
                    // aqui caso a pessoa clique mais de uma vez 
                    // em comprar adicionamos + 1 a quantidade do produto
                    draft.items[productInCartIndex].quantity++;
                }
                else {
                    draft.items.push({
                        product: product_1,
                        quantity: 1,
                    });
                }
                break;
            }
            case "INCREMENT_PRODUCT": {
                var productId_1 = action.payload.productId;
                var productInCartIndex = draft.items.findIndex(function (items) {
                    return items.product.id === productId_1;
                });
                draft.items[productInCartIndex].quantity++;
                break;
            }
            case "DECREMENT_PRODUCT": {
                var productId_2 = action.payload.productId;
                var productInCartIndex = draft.items.findIndex(function (items) {
                    return items.product.id === productId_2;
                });
                draft.items[productInCartIndex].quantity--;
                break;
            }
            case "REMOVE_PRODUCT": {
                var productId_3 = action.payload.productId;
                var productInCartIndex = draft.items.findIndex(function (items) {
                    return items.product.id === productId_3;
                });
                draft.items.splice(productInCartIndex, 1);
                break;
            }
            default: {
                return draft;
            }
        }
    });
};
export default cart;
